<template>
  <section class="section section-shaped section-lg my-0 vh100">
    <div class="shape shape-style-1 bg-gradient-default">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div class="container pt-lg-md">
      <div class="row justify-content-center">
        <div class="col-lg-5">
          <card type="secondary" shadow
                header-classes="bg-white pb-5"
                body-classes="px-lg-5 py-lg-5"
                class="border-0">
            <template>
              <template>
                <div class="text-center">
                  <router-link slot="brand" to="/">
                    <img src="/img/logo/logo-bk.png" class="logo" alt="logo">
                  </router-link>
                </div>
              </template>
            </template>
            <template>
              <div class="text-center text-muted mb-4 mt-2">
                <small>Yeni Bir Namerator Hesab Oluturun!</small>
              </div>
              <form role="form" @keyup.enter="register">
                <base-input alternative v-model="name"  @click="errorMessage=''"
                            class="mb-3"
                            placeholder="Ad-Soyad"
                            addon-left-icon="ni ni-hat-3">
                </base-input>
                <base-input alternative v-model="email"  @click="errorMessage=''"
                            class="mb-3"
                            placeholder="E-Posta"
                            addon-left-icon="ni ni-email-83">
                </base-input>
                <base-input alternative v-model="password"  @click="errorMessage=''"
                            type="password"
                            placeholder="ifre"
                            addon-left-icon="ni ni-lock-circle-open">
                </base-input>
                <base-input alternative v-model="passwordConfirmation"  @click="errorMessage=''"
                            type="password"
                            placeholder="ifre Tekrar"
                            addon-left-icon="ni ni-lock-circle-open">
                </base-input>
                <base-alert type="danger" v-text="errorMessage" v-if="errorMessage"></base-alert>
                <div class="text-center">
                  <base-button @click.prevent="register" type="primary" class="my-2">HESAP OLUTUR</base-button>
                </div>
              </form>
              <div class="text-center small text-muted mt-2">
                Hesabnz var m?
                <router-link :to="{name:'login'}">Giri Yapn</router-link>
              </div>
            </template>
          </card>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data: () => {
    return {
      name: "",
      email: "",
      password: "",
      passwordConfirmation: "",
      errorMessage: ""
    }
  },
  methods: {
    register() {
      this.$store
          .dispatch('register', {
            name: this.name,
            email: this.email,
            password: this.password,
            'password_confirmation': this.passwordConfirmation
          })
          .then((e) => {
            this.$router.push({name: 'profile'})
          })
          .catch((err) => {
            this.errorMessage = err.response.data.message[0];
          })
    }
  }
};
</script>
